import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from '../service/all-day-out-calls.service';

@Component({
  selector: 'dayout-deals',
  templateUrl: './deals.component.html',
  styleUrls: ['./deals.component.scss']
})
export class DealsComponent implements OnInit {

  constructor(private mainService: AllDayOutCallsService) { }

  hostname = this.mainService.hostname;
  data: any;
  ngOnInit(): void {
    this.topLifeOfTraveller()
  }

  topLifeOfTraveller() {
    this.mainService.deals().subscribe(res => {
      this.data = res;
    })
  }

}
