import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';

@Component({
  selector: 'dayout-featured',
  templateUrl: './featured.component.html',
  styleUrls: ['./featured.component.scss']
})
export class FeaturedComponent implements OnInit {

  constructor(private mainService: AllDayOutCallsService) { }
  hostname = this.mainService.hostname;
  loader:boolean = false;
  data: any;
  ngOnInit(): void {
    this.featuredService()
  }

  featuredService() {
    this.loader = true;
      this.mainService.topFeatured().subscribe(res => {
        this.data = res;
        this.loader =false;
      },err =>{
        this.mainService.isVisible = true;
        this.loader =false;
        console.log(err)
        alert("Looks")
      })
  }

}
