import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';

@Component({
  selector: 'dayout-top-experience',
  templateUrl: './top-experience.component.html',
  styleUrls: ['./top-experience.component.scss']
})
export class TopExperienceComponent implements OnInit {

  constructor(private mainService: AllDayOutCallsService) { }

  hostname = this.mainService.hostname;
  data: any;
  ngOnInit(): void {
    this.topLifeOfTraveller()
  }

  topLifeOfTraveller() {
    this.mainService.topExperience().subscribe(res => {
      this.data = res;
    })
  }

}
