import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';


@Component({
  selector: 'dayout-host-form',
  templateUrl: './host-form.component.html',
  styleUrls: ['./host-form.component.scss']
})
export class HostFormComponent implements OnInit {
  data:any;
  loader:boolean = false;
  get userName() {
    return this.hostForm.get("userName");
  }
  get phoneNumber() {
    return this.hostForm.get("phoneNumber");
  }
  get message() {
    return this.hostForm.get("message");
  }

  constructor(private fb :FormBuilder,private mainService: AllDayOutCallsService) { }
  date: Date = new Date();

  hostForm = this.fb.group({
    userName: ['',[Validators.required, Validators.minLength(3)]],
    phoneNumber: ['',[Validators.required, Validators.minLength(10),Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
    message: ['',Validators.required],
  })

  ngOnInit(): void {
  }
  
  onSubmit() {
      let dataObj = this.hostForm.value;
      dataObj["date"] = this.date.toLocaleString();
      this.mainService.sendHost(dataObj).subscribe(res => {
        this.data = res;
        this.loader =false;
        this.hostForm.reset();
        alert( this.data.message);
      },err =>{
        this.mainService.isVisible = true;
        this.loader =false;
        console.log(err)
      })
  }
}
