import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';

@Component({
  selector: 'day-host',
  templateUrl: './host.component.html',
  styleUrls: ['./host.component.scss']
})
export class HostComponent implements OnInit {
  data:any
  constructor(private mainService: AllDayOutCallsService) { }


  ngOnInit(): void {
    this.mainService.getHost().subscribe(res =>{
      this.data = res;
    })
  }

  deleteData(item:any){
     this.mainService.deleteHostDetails(item).subscribe(res =>{
      alert("The given Item is deleted")
      this.mainService.getHost().subscribe(res =>{
        this.data = res;
      })
    })
  }

}
