import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';

@Component({
  selector: 'dayout-top-deals',
  templateUrl: './top-deals.component.html',
  styleUrls: ['./top-deals.component.scss']
})
export class TopDealsComponent implements OnInit {

  constructor(private mainService: AllDayOutCallsService) { }

  hostname = this.mainService.hostname;
  data: any;
  ngOnInit(): void {
    this.topDealsUnique()
  }

  topDealsUnique() {
    this.mainService.topDealsUnique().subscribe(res => {
      this.data = res;
    })
  }
}
