import { Component, OnInit } from '@angular/core';
import { AllDayOutCallsService } from 'src/app/service/all-day-out-calls.service';

@Component({
  selector: 'dayout-lief-of-traveller',
  templateUrl: './lief-of-traveller.component.html',
  styleUrls: ['./lief-of-traveller.component.scss']
})
export class LiefOfTravellerComponent implements OnInit {

  constructor(private mainService: AllDayOutCallsService) { }

  hostname = this.mainService.hostname;
  data: any;
  ngOnInit(): void {
    this.topLifeOfTraveller()
  }

  topLifeOfTraveller() {
    this.mainService.topLifeOfTraveller().subscribe(res => {
      this.data = res;
    })
  }

}
